import { useLayoutEffect, useState } from 'react';
import './ProgressBar.css';
import sectionsAV from './sectionsAV.json';
import { Tooltip } from 'react-tooltip';

const ProgressBar = () => {
    const [scroll, setScroll] = useState(0);
    const [sections, setSections] = useState([]);
    const [tooltips, setTooltips] = useState([]);

    const handleScroll = () => {
        const winScroll =
            document.body.scrollTop || document.documentElement.scrollTop;
        const height =
            document.documentElement.scrollHeight -
            document.documentElement.clientHeight;
        const scrolled = (winScroll / height) * 100;
        setScroll(scrolled);
    };

    useLayoutEffect(() => {
        const calculateKeypointPosition = (section) => {
            const element = document.getElementById(section);
            if (element) {
                const elementPosition = element.offsetTop;
                const totalHeight =
                    document.documentElement.scrollHeight -
                    document.documentElement.clientHeight;
                return (elementPosition / totalHeight) * 100;
            }
            return 0;
        };

        const divs = sectionsAV.map(({ sectionName, sectionID }) => {
            return <div
                data-tooltip-id={sectionID}
                data-tooltip-content={sectionName}
                key={sectionID}
                className="progress-keypoint"
                style={{ left: `${calculateKeypointPosition(sectionID)}%` }}
                onClick={() => handleAnchorClick(sectionID)} 
            />
        });
        const tooltips = sectionsAV.map(({ sectionID }) => {
            return <Tooltip id={sectionID} key={sectionID} style={{ zIndex: '9992' }}/>
        })

        setSections(divs);
        setTooltips(tooltips);
        window.addEventListener('scroll', handleScroll);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    const handleAnchorClick = (id) => {
        if (id) {
            const element = document.getElementById(id);
            if (element) {
                element.scrollIntoView({ behavior: 'smooth' });
            }
        }
    };

    return (
        <div className="progress-bar-container">
            <div className="progress-bar" style={{ width: `${scroll}%` }} />
            {sections && sections}
            {tooltips && tooltips}
        </div>
    );
};

export default ProgressBar;
